<template>
  <div>
      <el-button @click="newdata()">新增参数</el-button>
      当前编辑: {{templatename}}
    

    <el-dialog :visible.sync="dialogVisible" width="30%">
      <span>新增参数</span>

      <!-- <el-input placeholder="被修改的参数的ID" v-model="adddataform.id">
    <template slot="prepend">被修改的参数的ID</template>
  </el-input> -->

      <el-input placeholder="参数名" v-model="adddataform.name">
        <template slot="prepend">参数名</template>
      </el-input>

      <el-input placeholder="字段名" v-model="adddataform.fieldname">
        <template slot="prepend">参数{字段名}</template>
      </el-input>

      <el-input placeholder="小数位" v-model="adddataform.decimal">
        <template slot="prepend">参数{小数位}</template>
      </el-input>

      <el-input placeholder="后缀" v-model="adddataform.ext">
        <template slot="prepend">参数{后缀}</template>
      </el-input>

      <el-input placeholder="后缀" v-model="adddataform.default">
        <template slot="prepend">参数{默认值}</template>
      </el-input>
      
      <br />
      <el-button type="primary" @click="pushaddnewdata()">确 定</el-button>
    </el-dialog>

    <div class="module">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="id" label="ID" width="100" align="center">
        </el-table-column>

        <el-table-column prop="decimal" label="小数位" width="100" align="center">
        </el-table-column>

        <el-table-column prop="ext" label="后缀名" width="150" align="center">
        </el-table-column>

        <el-table-column prop="fieldname" label="参数字段名" align="center">
        </el-table-column>

        <el-table-column prop="create_time" label="创建时间" width="250" align="center">
        </el-table-column>

        <el-table-column prop="name" label="参数名"> </el-table-column>

        <el-table-column prop="default" label="默认值"> </el-table-column>

        <el-table-column label="更多" fixed="right" width="125">
          <template slot-scope="scope">
            <el-dropdown>
              <el-button type="primary">
                更多<i class="el-icon-arrow-down el-icon--right"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item @click.native="tabsedititem(scope.row)"
                  >编辑信息</el-dropdown-item
                >
                <el-dropdown-item @click.native="manager(scope.row.id, 1)"
                  >删除</el-dropdown-item
                >
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { Itemlist, Itemadd, Itemmanager } from "@/api/paramtemplate";
export default {
  name: "Home",
  data() {
    return {
      tableData: [],
      dialogVisible: false,
      templatename: null,
      adddataform: {
        id: null,
        name: null,
        categoryid: null,
        fieldname: null,
        ext: null,
        decimal: 0,
        default: null
      },
    };
  },
  components: {},
  methods: {
    newdata() {
      this.dialogVisible = true;
    },
    pushaddnewdata() {
      this.$http(Itemadd(this.adddataform), (res) => {
        console.log(res);
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            type: "success",
          });
          this.getList();
          this.adddataform.id = null;
          this.adddataform.name = null;
          this.adddataform.fieldname = null;
          this.adddataform.ext = null;
          this.adddataform.decimal = 0;
          this.adddataform.default = null;
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
      this.dialogVisible = false;
    },
    tabsedititem(data) {
      this.adddataform.id = data.id;
      this.adddataform.name = data.name;
      this.adddataform.fieldname = data.fieldname;
      this.adddataform.ext = data.ext;
      this.adddataform.decimal = data.decimal;
      this.adddataform.default = data.default;
      this.newdata();
    },
    getList() {
      this.$http(
        Itemlist({
          categoryid: this.adddataform.categoryid,
        }),
        (res) => {
          console.log(res);
          if (res.code == 200) {
            this.$message({
              message: res.msg,
              type: "success",
            });
            this.tableData = res.data;
          } else {
            this.$message({
              message: res.msg,
              type: "error",
              duration: 5 * 1000,
            });
          }
        }
      );
    },
    manager(id, set) {
      this.$http(
        Itemmanager({
          id: id,
          set: set,
        }),
        (res) => {
          console.log(res);
          if (res.code == 200) {
            this.getList();
          } else {
            this.$message({
              message: res.msg,
              type: "error",
              duration: 5 * 1000,
            });
          }
        }
      );
    },
  },
  created() {
    this.adddataform.categoryid = this.$route.query.categoryid;
    this.templatename = this.$route.query.templatename;
    this.getList();
  },
};
</script>
<style lang="scss" scoped>
</style>